import { WebPlugin } from '@capacitor/core';

import type { CapMindboxPlugin, InitializationData, ExecuteSyncOperationPayload, ExecuteAsyncOperationPayload, InAppCallback } from './definitions';

export class CapMindboxWeb extends WebPlugin implements CapMindboxPlugin {
  async echo(options: { value: string }): Promise<{ value: string }> {
    console.log('ECHO', options);
    return options;
  }

  async initialize(options: InitializationData): Promise<void> {
    console.log('Initialize', options);
  }

  async getDeviceUUID(): Promise<{ deviceUUID: string }> {
    console.log('getDeviceUUID');
    return { deviceUUID: 'mock-device-uuid' };
  }

  async getAPNSToken(): Promise<{ token: string }> {
    console.log('getAPNSToken');
    return { token: 'mock-token' };
  }

  async updateToken(options: { token: string }): Promise<void> {
    console.log('updateToken', options);
  }

  async registerInAppCallbacks(callbacks: InAppCallback[]): Promise<void> {
    console.log('registerInAppCallbacks', callbacks);
  }

  async executeAsyncOperation(options: ExecuteAsyncOperationPayload): Promise<void> {
    console.log('executeAsyncOperation', options);
  }

  async executeSyncOperation(options: ExecuteSyncOperationPayload): Promise<any> {
    console.log('executeSyncOperation', options);
    return { data: 'mock-data' };
  }

  async setLogLevel(options: { level: number }): Promise<void> {
    console.log('setLogLevel', options);
  }

  async getSdkVersion(): Promise<{ sdkVersion: string }> {
    console.log('getSdkVersion');
    return { sdkVersion: 'mock-sdk-version' };
  }

  async pushDelivered(options: { uniqKey: string }): Promise<void> {
    console.log('pushDelivered', options);
  }

  async updateNotificationPermissionStatus(options: { granted: boolean }): Promise<void> {
    console.log('updateNotificationPermissionStatus', options);
  }
}